.snapContainer {
  scroll-snap-type: y mandatory;
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  overflow: scroll;
}
.snapSection {
  border-right: 1px solid white;
  min-width: 100vw;
  height: 100vh;
  scroll-snap-align: start;
  text-align: center;
  position: relative;
}
.section{
  scroll-snap-align: start;
}

.AppContainer {
  scroll-snap-align: start;
}

.App {
  scroll-snap-align: start;
}

.App-logo {
  height: 20vmin;
  margin-block: 10vh;
  pointer-events: none;
}

.App-logo{
  height: 20vmin;
  margin-block: 7vh;
  pointer-events: none;
}

.App-logo2 {
  height: 10vmin;
  margin-block: 7vh;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
  .App-logo2 {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
